/* React Imports */
import React, {useContext, useEffect, useState} from 'react'
import { Helmet } from 'react-helmet'

/* Material Imports */
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import { Typography } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Twap from './Twap';
import {BackgroundContext} from "../App";
import moon from '../images/moon-footer.png';
import stars from '../images/stars.png';
import lambo from '../images/lambo-sideview.webp';

/* CSS Styles */
const useStyles = makeStyles((theme) => ({
  wrapper: {
    height: '100%',
    width: '100%',
    position: 'relative',
    alignItems: 'center',
    zIndex: 10,
  },
  contentWrapper: {
    margin: `${theme.spacing(5)}px auto`,
    maxWidth: '600px',
    padding: theme.spacing(3),
    color: 'white',
    backgroundColor: '#191919',
    zIndex: "10",
  },
  price: {
    textAlign: 'center',
    padding: theme.spacing(2),
  },
  claim: {
    textAlign: 'center',
    padding: theme.spacing(3),
  },
  twapWrapper: {
    height: '100%',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: theme.spacing(5),
  },
  twap: {
    width: '400px',
    height: '400px',
  },
  background: {
    position: 'absolute',
    width: '100%',
    height: '100%',
    overflow: 'hidden',
  },
  lamboWrapper: {
    width: "100%",
    overflowX: "hidden",
    position: "relative",
  },
  lambo: {
    width: 600,
    height: 'auto',
    transform: 'scaleX(-1)',
    bottom: '10px',
    margin: '0 auto',
    marginLeft: "100px",
    transition: "margin 1s ease-out",
    [theme.breakpoints.up('md')]:{
      width: 800,
    },
    [theme.breakpoints.up('lg')]:{
      width: 1000,
    },
    "&:hover":{
      marginLeft: "600px",
    }
  },
  fire: {
    position: 'absolute',
    width: '700px',
    height: 'auto',
    bottom: '10px',
    right: '40%',
  },
  text: {
    marginLeft: "300px",
    position: "absolute",
    top: "50%",
  }
}))

function NitroPage() {
  const classes = useStyles()
  const backgroundContext = useContext(BackgroundContext);
  const [buyPrice, setBuyPrice] = useState(0);
  const [sellPrice, setSellPrice] = useState(0);
  const [twap, setTwap] = useState(0);
  const [availableNitro, setAvailableNitro] = useState(0);

  useEffect(()=>{
    backgroundContext.setSpace(stars);
    backgroundContext.setGround(moon);
  },[])

  return (
    <div className={classes.wrapper}>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Nitro | Lambo Degenerates</title>
      </Helmet>
      <Grid container style={{ alignItems: 'center' }}>
        <Grid item xs={12} md={6}>
          <Box
            px={3}
            className={classes.contentWrapper}
            borderRadius={'borderRadius'}
          >
            <Typography component={'h1'} variant={'h2'} align={'center'}>
              Nitro
            </Typography>
            <Box p={2}>
              <Typography variant={'p'} component={"p"}>
                Buy any $LAMBO recently on Uniswap? Your tokens will show up below.
              </Typography>
              <Typography variant={'p'} component={"p"}>
                Monitor the buy price and sell price with ease and check out the nitro gauge to the right, which measures the current % reward to buyers and current % punishment for sellers.
              </Typography>
              <Typography variant={'p'} component={"p"}>
                Itching to get an edge in the game? Learn more nerdy details on our <a href={"https://lambodegens.medium.com/lambo-boosting-the-trust-and-value-in-the-defi-world-6fc8c369f7bc"} target={"_blank"}>Medium account</a>.
              </Typography>
            </Box>
            <Grid container>
              <Grid item xs={6} className={classes.price}>
                <Typography variant={'h4'}>Buy price: {buyPrice}</Typography>
              </Grid>
              <Grid item xs={6} className={classes.price}>
                <Typography variant={'h4'}>Sell price: {sellPrice}</Typography>
              </Grid>
            </Grid>
            <Box className={classes.claim}>
              <Button
                variant="contained"
                color={"primary"}
                size={'large'}
                style={{
                  padding: '18px 36px',
                  fontSize: '18px',
                }}
              >
                Claim {availableNitro} LAMBO
              </Button>
            </Box>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} className={classes.twapWrapper}>
          <Box className={classes.twap}>
            <Twap twap={twap} />
          </Box>
        </Grid>
      </Grid>
      <Box className={classes.lamboWrapper}>
        <Typography className={classes.text}>Ask about Dosh</Typography>
        <img src={lambo} className={classes.lambo}/>
      </Box>
    </div>
  )
}

export default NitroPage
